
/* @todo | This should probably not be named after the
 * @todo | EC if we want cards to be reusable in other sections.
 */
.ec-section .layout_five-up-left-primary a.card-image-landscape {
  @include breakpoint($bp-large) {
    height: 100%;
  }
}

// Styling for cards when they're in a Four Up layout.

.layout-four-up .layout_item {
  @include breakpoint($bp-med) {
    margin-bottom: 0;
  }
}

.layout-four-up .card-image-landscape img {
  @include breakpoint($bp-med) {
    width: 100%;
    height: 100%;
  }
}

.layout_five-up-left .card-image-landscape,
.layout_three_up .card-image-landscape {
  @include breakpoint($bp-small $bp-med) {
    display: flex;
    flex-direction: column;
  }
}

.layout_five-up-left .card-image-landscape_overlay,
.layout_three_up .card-image-landscape_overlay {
  @include breakpoint($bp-small $bp-med) {
    position: relative;
    background: $black-7;
    flex-grow: 1;
    padding: 12px 0;
  }
}
